const buttons = document.querySelectorAll('.filters button')
const typeButtons = document.querySelectorAll('.filters .type button')
const logicButtons = document.querySelectorAll('.filters .logic button')

typeButtons.forEach(function (btn) {
  btn.addEventListener('click', function () {
    filterType(this)
  })
})

logicButtons.forEach(function (btn) {
  btn.addEventListener('click', function () {
    filterLogic(this)
  })
})

function filterType (btn) {
  const logos = document.querySelectorAll('.logos a')
  const attrId = btn.getAttribute('id')

  activateBtn(btn, 0)
  showAll(logos)
  changeSortOrder(btn)

  logos.forEach(function (logo) {
    if (attrId != 'all') {
      if (logo.getAttribute('data-tags').indexOf(attrId) === -1) {
        logo.classList.add('hidden')
      }
    }
  })
}

function filterLogic (btn) {
  const logos = document.querySelectorAll('.logos a')
  const attrId = btn.getAttribute('id')
  activateBtn(btn, 1)
  removeOrder(logos)
  const sortOrder = changeSortOrder(btn)

  if (attrId == 'alphabet') {
    alphaOrder(logos, sortOrder)
  } else if (attrId == 'date') {
    dateOrder(logos, sortOrder)
  }
}

function showAll (logos) {
  logos.forEach(function (logo) {
    logo.classList.remove('hidden')
  })
}

function changeSortOrder (btn) {
  let sortOrder = 'up'
  if (btn.classList.contains('up')) {
    sortOrder = 'down'
  }

  resetOrder(btn)

  // Add new sortOrder
  btn.classList.add(sortOrder)
  return sortOrder
}

function resetOrder (btn) {
  // Remove sortOrder class
  btn.classList.remove('down')
  btn.classList.remove('up')
}

function alphaOrder (logos, sortOrder) {
  const tabLogos = []
  const logosL = logos.length
  logos.forEach(function (logo) {
    const txtLogo = logo.children[0].getAttribute('alt')
    tabLogos.push(txtLogo)
  })
  tabLogos.sort()
  if (sortOrder == 'down') tabLogos.reverse()

  for (let i = 0; i < logosL; i++) {}
  logos.forEach(function (logo) {
    const txtLogo = logo.children[0].getAttribute('alt')
    tabLogos.forEach(function (tabLogoTxt, i) {
      if (txtLogo == tabLogoTxt) {
        logo.style.order = i
      }
    })
  })
}

function dateOrder (logos, sortOrder) {
  const tabLogos = []
  logos.forEach(function (logo) {
    const txtLogo = logo.getAttribute('data-date')
    tabLogos.push(txtLogo)
  })
  tabLogos.sort()
  if (sortOrder == 'down') tabLogos.reverse()

  logos.forEach(function (logo) {
    const txtLogo = logo.getAttribute('data-date')
    tabLogos.forEach(function (tabLogoTxt, i) {
      if (txtLogo == tabLogoTxt) {
        logo.style.order = i
      }
    })
  })
}

function activateBtn (btn, status) {
  const active = 'active'

  // Type button
  if (status == 0) {
    typeButtons.forEach(function (b) {
      b.classList.remove(active)
    })
  }

  // Logic button
  else {
    logicButtons.forEach(function (b) {
      b.classList.remove(active)
      if (b.getAttribute('id') != btn.getAttribute('id')) {
        resetOrder(b)
      }
    })
  }

  btn.classList.add(active)
}

function removeOrder (logos) {
  logos.forEach(function (logo) {
    logo.style.order = 0
  })
}
